@font-face{
  font-family: avancement;
  src: url("./assets/avancement.otf");
}

html {
  overflow: scroll;
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

.App {
  text-align: center;
  font-family: avancement;
  background-color: #1B1B1E;
  z-index: 1;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  font-family: avancement;
  background-color: #373F51;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  color: white;
  z-index: 2;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  line-height: 2em;
}

#backend {
  margin-bottom: 10vw;
}

.headerText {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2em;
}

.App-link {
  color: #61dafb;
}

/* transition: target property_duration speed_curve */

.card {
  transition: transform 0.2s ease;
}

.contConfirm {
  margin: 0px !important;

  max-width: 700px !important;
}

.staticPic {
  position: absolute;
  z-index: 10;
  opacity: 100% !important;
}

.activePic {
  z-index: 1;
  width: 225px;
  height: 115px;
  background-size: 100% 100%;
}

.hoverCard:hover {
  transform: scale(1.1);
}

.carousel.carousel-slider {
  height: 80vh;
}

.carousel .slider.animated {
  transition: all 2s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.frontEnd {
  height: 100vh;
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
  overflow: scroll;
  scroll-snap-align: start;
}

.form-control{
  background-color: #343a40 !important;
  color: white !important;
  border-radius: 0 !important;
  border: 0 !important;
}

.form-control:focus {
  outline-width: 2px !important;
  outline-style: solid !important;
  outline-color: white !important;
}

#tsparticles {
  align-content: center;
  overflow: scroll;
  scroll-snap-align: start;
}

.skillFlex {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}

#portfolio .portfolioRow > h1 {
  width: 100%;
  margin-top: 10vh;
  margin-bottom: 5vh;
}

.frontEnd {
  scroll-snap-align: start;
}

#frontEnd .card-title, #backEnd .card-title {
  margin-bottom: 0;
}

.frontEnd .portfolioRow {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #1B1B1E;
}

/*#portfolio .card-title {
  margin-bottom: 1em !important;
  margin-top: 1em !important;
}*/

.frontEnd .card {
  max-width: 225px;
  margin: 1.2vw;
}

.card-title.h5 {
  font-weight: bold;
  margin-top: 0 !important;
  margin-bottom: 0.5em !important;
}

#portfolio .card {
  min-height: 450px;
  max-height: 450px;
} 

#portfolio .card-text {
  font-size: 0.9rem;
}

#portfolio .list-group-item {
  font-size: 0.9rem;
}

#portfolio .card-body {
  padding-left: 0.525em;
  padding-right: 0.525em;
  max-height: 7rem;
}

h1.portfolioHeader {
  text-align: center;
}

#portfolio .carousel .slide .card {
  max-height: 500px !important;
}

#portfolio .carousel .slide.selected .card {
  max-height: 500px !important;
}

/*#portfolio .carousel .slider-wrapper{
  box-shadow: inset 12px 12px 2px 1px rgba(0, 0, 0, .2);
}*/



.portfolioTitle {
  text-align: center;
}

#listp1 > .list-group-item {
  background-color: #343a40!important;
}

.catTitle {
  text-align: left;
  margin-right: 4.0vw;
}
.catTitle h1 {
  margin-bottom: 0;
}

#tsparticles {
  z-index: -2;
}

#tsparticles canvas {
  z-index: 5;
  height: 1vh;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: 5s ease 0s normal forwards 1 fadein;
  animation: 5s ease 0s normal forwards 1 fadein;
}

@keyframes fadein{
  0% { opacity:0; }
  66% { opacity:0; }
  100% { opacity:1; }
}

@-webkit-keyframes fadein{
  0% { opacity:0; }
  66% { opacity:0; }
  100% { opacity:1; }
}

.nav-link {
  font-size: 3vh;
  display: block;
}

.mr-auto > div{
  display: flex; 
  margin-bottom: 0;
}

.nav-link.active {
  font-weight: 600;
  color: #58A4B0;
}

.px-4:hover {
  color: #58A4B0 !important; /* use this somewhere: #dc2f02 */
  text-decoration: none !important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
